import { InputCommon } from '../../common/input';
import { Selector } from '../../common/selector';
import { useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import { Result } from '../../common/result';
import Grid from '@mui/material/Grid';
import { Phois,kvItems } from './data_drilling211';

export const Drilling211 = () => {
    const [result, setResult] = useState(0);
    const [phois, setPhois] = useState(Phois);
    const [phoi, setPhoi] = useState(0);
    const [selectedPhoi, setSelectedPhoi] = useState(false);

    const [kc, setKc] = useState(0);
    const [z, setZ] = useState(0);

    const [kv, setKv] = useState(0);
    const [selectedKv, setSelectedKv] = useState(false);

    const [kver, setKver] = useState(1.3);
    const [kst, setKst] = useState(1.2);
    const [h, setH] = useState(0);

    const handleOnClick = () => {
        setResult(0);
        setKc(0);
        setZ(0);
        document.getElementById('khoan3.1.1').reset();
    };

    useEffect(() => {
        const k = 1 / Math.pow(h, z) * kc * kv * kst * kver;
        setResult(k);
    }, [kc, z, h, kv, phoi]);

    useEffect(() => {
        setKc(Phois?.find(item => item.value === phoi)?.kc11.value)
        setZ(Phois?.find(item => item.value === phoi)?.z.value)
    }, [phoi]);

    return (
        <form id="khoan3.1.1">
            <div style={{ display: 'flex', flexDirection: 'column'}}>
                <h4><b>2.1 Lc ct n v k<sub>c</sub></b></h4>
                <Grid container spacing={2}>
                    <Grid item sm={12} md={6} lg={4}> 
                        <Selector text="Phi" items={phois} setSelected={setSelectedPhoi} setValue={setPhoi} disable={true}/>
                    </Grid>
                    <Grid item sm={12} md={6} lg={4}>
                        <InputCommon text="Lc ct n v kc1.1 " defaultValue={kc} setValue={setKc} disable={true}/>
                    </Grid>
                    <Grid item sm={12} md={6} lg={4}>  
                        <InputCommon text="Hng s vt liu z" defaultValue={z} setValue={setZ} disable={true}/>
                    </Grid>
                    <Grid item sm={12} md={6} lg={4}>  
                        <InputCommon text="Chiu dy lp ct" placeholder="Nhp thng s" setValue={setH}/>
                    </Grid>
                    <Grid item sm={12} md={6} lg={4}> 
                        <Selector text="H s hiu chnh tc  ct: Kv" setSelected={setSelectedKv} disable={true} setValue={setKv} items={kvItems}/>
                    </Grid>
                    <Grid item sm={12} md={6} lg={4}>  
                        <InputCommon text="H s hao mn Kver" defaultValue={kver} setValue={setKver} disable={true}/>
                    </Grid>
                    <Grid item sm={12} md={6} lg={4}>  
                        <InputCommon text="H s nn phoi Kst" defaultValue={kst} setValue={setKst} disable={true}/>
                    </Grid>
                </Grid>
                <Result  result={result} text="Lc ct n v kc" unit="N/mm2" />
                <br></br>
                <Button variant="outlined" style={{ margin: '0 auto' }} onClick={handleOnClick} >Reset</Button>
            </div>
        </form>
    );
};